function App() {
  //* All our clients are in this state
  const [Json, SetJson] = useState([]);

  const APIURL = "https://randomuser.me/api/?format=json&results=1";

  useEffect(async => {
    fetch(APIURL)
      .then(response => {
        return response.json();
      })
      .then(myJson => {
        SetJson(myJson);
      });
  }, []);

  useEffect(() => {
    if (typeof Json.results !== "undefined") console.log(Json.results[0]);
  }, [Json]);

  if (typeof Json.results !== "undefined") {
    return (
      <div>
        <header id="Header">
          <h1>PEOPLE</h1>
        </header>
        <MainContainerComponent data={Json.results[0]} />
        <footer id="Footer"></footer>
      </div>
    );
  } else {
    return <p>Loading...</p>;
  }
}

export default App;
