import React, { useState } from "react";

import NameComponent from "./NameComponent";
import InfoComponent from "./InfoComponent";

function ProfileComponent(props) {
  return (
    <div id="ProfileComponent">
      <NameComponent name={props.data.name} />
      <InfoComponent
        info={props.data.email}
        image={require("../images/email-12-64.png")}
      />
      <InfoComponent
        info={props.data.phone}
        image={require("../images/phone-46-64.png")}
      />
      <InfoComponent
        info={"@" + props.data.name.first + props.data.name.last}
        image={require("../images/twitter-4-64.png")}
      />
      <InfoComponent
        info={props.data.location.city + ", " + props.data.location.country}
        image={require("../images/pin-8-64.png")}
      />
    </div>
  );
}

export default ProfileComponent;
